class ApplicationModel {

  constructor({
    name,
    platform,
    tools,
    logo,
    description,
    images,
    does,
  }) {
    this.does = does
    this.name = name
    this.platform = platform
    this.tools = tools
    this.description = description
    this.images = images
    this.logo = logo
  }
}

export default ApplicationModel