<template>
  <div class="application-page">
    <div id="applist-container">
       <div id="list-background" />
       <div id="list-container">
         <div v-for="(item, index) in dataSource" 
         :key="index" 
         class="application-item-container"
         :class="{ selectedAppRow: index === selectedAppIndex,'application-item-container-hover': index !== selectedAppIndex }"
         @click="selectApp(index)"
         >
         
         <img :src="item.logo" /> <span id="name"> {{ item.name }}</span>
         <span id="divider" v-if="selectedAppIndex-1 !== index"/>
      </div>
       </div>
    </div>
     <div id="app-details-container">
       <div id="app-details-header">
         <img :src="getSelectedApp.logo"/>
         <div id="details-container">
           <h1> {{getSelectedApp.name}} </h1>
          <span> <b> Platform: </b>  {{getSelectedApp.platform}}</span>
           <div id="tools-container">
             <b> Tools: </b>
             <span class="tools-item-container" 
             v-for="(tool, index) in getSelectedApp.tools" :key="index"
             >
               <img :src="tool.imgSrc" />
               <span> {{ tool.name }} </span>
             </span>
           </div>
         </div>
         
       </div>
      <div id="app-desc-container" transition="fade">
           {{getSelectedApp.description}}
           <ul :style="{'column-count': getSelectedApp.does.length > 5 ? 2 : 1}">
             <li v-for="(doItem, indexDo) in getSelectedApp.does" :key="indexDo">
               {{doItem}}
             </li>
           </ul>
      </div>
      <div id="app-ss-container">
         <span v-for="(src, indexSrc) in getSelectedApp.images" :key="indexSrc">

            <img :src="src" v-if="isImage(src)">
            <video v-else autoplay oncanplay="this.muted=true">
            <source :src="src" type="video/mp4">
            Your browser does not support the video tag.
          </video>
         </span>
      </div>
    </div>
  </div>
</template>


<script>
import dataSource from "@/models/mockApplicationModel.js"
export default {
  methods: {
    isImage(path) {
      const ext = path.slice((path.lastIndexOf(".") - 1 >>> 0) + 2).toLowerCase()
      if(ext === "png" || ext === "jpg") {
        return true
      }
      return false
    },
    selectApp(index) {
      this.selectedAppIndex = index
    }
  },
  computed: {
    getSelectedApp() {
      return this.dataSource[this.selectedAppIndex]
    }
  },
  data () {
    return {
      dataSource,
      selectedAppIndex: 0
    }
  },
  
}
</script>

